.payment{
  &-succes{
    display: flex;
    align-items: center;
    justify-content: center;

    height: 100%;
    &__wrapper{
      max-width: 370px;
      width: 100%;
    }
    &__title{
      text-align: center;
    }
    &__img{
      width: 100px;
      height: 100px;
      margin: 50px auto 0 auto;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
    &__text{
      margin-top: 50px;

      font-size: 18px;
      line-height: 24px;
      color:$monoBlack;
      text-align: center;
    }
    &__btn{
      margin-top: 35px;

      height: 60px;
    }
  }
}