.course{
  &-single{
    &__grid{
      display: grid;
      grid-template-columns: auto 370px;
      gap: 90px;


      background: #FFFFFF;
      box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.08);
      border-radius: 15px;

      overflow: hidden;

      &-content{
        padding: 40px;
        &-block{
          & + &{
            margin-top: 30px;
          }
        }
      }
    }

    &__desc{
      font-size: 18px;
      line-height: 24px;
      color: $monoBlack;
    }
    &__status{
      &-text{
        color: $secondary;
        font-size: 18px;
      }
      &-progress{
        margin-top: 15px;
      }
    }
    &__content{
      margin-top: 30px;
    }
    &__btn{
      height: 60px;
      max-width: 270px;
      &-check{
        height: 40px;
        max-width: 225px;

        border-radius: 21px;

        &::before{
          content: '';
          width: 20px;
          height: 20px;
          display: block;

          background: url("../img/clock-orange.svg") center center/contain no-repeat;

          margin-right: 10px;
        }
      }
    }
    &__img{
      width: 100%;
      height: 100%;

      &-item{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__description{
      max-width: 670px;
      ul{
        list-style: disc inside;
      }
      &-title{
        font-weight: 600;
        font-size: 21px;
        line-height: 25px;
        letter-spacing: -0.2px;

        color:$monoBlack;

        margin-bottom: 30px;
      }
      p{
        font-size: 18px;
        line-height: 24px;

        color:$monoBlack;
      }

      p + p{
        margin-top: 30px;
      }
      p + ul{
        margin-top: 30px;
      }
      ul + p{
        margin-top: 30px;
      }
    }
    &__modules{
      &-item + &-item{
        margin-top: 40px;
      }
    }
  }
  &-info{
    &__wrapper{
      display: flex;
    }
    &__item{
      & + &{
        margin-left: 60px
      }
    }
    &__title{
      font-weight: 600;
      font-size: 21px;
      line-height: 25px;
      letter-spacing: -0.2px;

      color: $monoBlack;
    }
    &__text{
      margin-top: 5px;

      font-size: 18px;
      line-height: 24px;
      color: $monoBlack;
    }
  }
  &-module{
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &.active{
        .course-module__title{
          &::before{
            transform: rotate(0deg);
          }
        }
      }
    }
    &__title{
      font-weight: 600;
      font-size: 25px;

      color:$monoBlack;

      letter-spacing: -0.2px;

      display: flex;
      align-items: center;
      span{
        margin-left: 5px;
      }
      &::before{
        content: '';
        width: 36px;
        height: 36px;
        display: block;

        background: url("../img/module-arrow.svg") center center/contain no-repeat;

        margin-right: 20px;

        transform: rotate(-90deg);

        transition: .3s all;
      }
    }
    &__status{
      display: flex;
      align-items: center;
      &-progress{
        margin-left: 10px;

        width: 380px;
      }
      &-text{
        font-size: 18px;
        color:$secondary;
      }
    }
    &__content{
      margin-top: 40px;
    }
  }
}

.lesson{
  &-grid{
    display: grid;
    grid-template-columns: repeat(4,minmax(0,1fr));
    grid-auto-rows: 275px;
    gap: 40px 30px;
    &__item{
      height: 100%;
    }
  }
  &-item{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover{
      .lesson-item__desc{
        color:$accent;
      }
    }
    &_disabled{
      opacity: .5;
      pointer-events: none;
      .lesson{
        &-item{
          &__progress{
            display: none;
          }
          &__date{
            display: flex;
          }
        }
      }
    }
    &__wrapper{
      height: 100%;
      padding: 20px;

      background: #FFFFFF;
      box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.08);
      border-radius: 25px;

      display: flex;
      flex-direction: column;

      transition: 0.3s all ;
      &:hover{
        box-shadow: 0px 4px 45px rgba(0, 0, 0, 0.1);
      }
    }
    &__title{
      font-size: 18px;
      line-height: 24px;

      color:$secondary;
    }
    &__status{
      margin-top: 15px;

      padding: 8px 16px;
      border-radius: 20px;
      width: max-content;
      font-size: 16px;
      &-completed{
        color:$secondary;
        background: #EBEBEB;
      }
      &-failed{
        color:$accent;
        background-color: $accentSecondary;
      }
    }
    &__content{
      margin-top: 15px;
    }
    &__desc{
      font-size: 18px;
      line-height: 24px;

      color:$monoBlack;

      transition: .5s color;
    }
    &__footer{
      padding-top: 35px;
      margin-top: auto;

      width: 100%;
    }
    &__progress{
      width: 100%;
    }
    &__date{
      display: none;

      align-items: center;

      &::before{
        content: '';
        width: 18px;
        height: 18px;
        display: block;
        margin-right: 10px;

        background: url("../img/lock.svg") center center/contain no-repeat;
      }
      &-text{
        color:$secondary
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .course{
    &-single{
      &__grid{
        grid-template-columns: minmax(0,1fr);

        gap: 30px;
        &-content{
          order: 2;

          padding: 0 40px 40px 40px;
        }
      }
      &__img{
        order: 1;

        height: 370px;
      }

    }
    &-module{
      &__header{
        flex-direction: column;
        align-items: flex-start;
      }
      &__status{
        margin-top: 30px;
      }
    }
  }
  .lesson-grid{
    grid-template-columns: repeat(2,minmax(0,270px));
  }
}


@media screen and (max-width: 767px) {
  .course{
    &-info{
      &__wrapper{
        flex-direction: column;
      }
      &__item{
        & + &{
          margin: 30px 0 0 0;
        }
      }
    }
    &-module{
      &__title{
        flex-wrap: wrap;
        span{
          width: 100%;
          padding-left: 56px;
        }
      }
    }
  }
}


@media screen and (max-width: 600px) {
  .lesson{
    &-grid{
      grid-template-columns: minmax(0,1fr);
      grid-auto-rows: auto;
      gap: 30px;
    }
  }
  .course{
    &-module{
      &__status{
        width: 100%;
      }
    }
  }
}