.profile{
  &-info{
    &__wrapper{
      display: flex;
      align-items: center;
    }
    &__img{
      width: 75px;
      height: 75px;
      flex-shrink: 0;

      border-radius: 50%;
      overflow: hidden;
      &-item{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__content{
      margin-left: 15px;
    }
    &__text{
      font-weight: 600;
      font-size: 25px;
      line-height: 30px;

      letter-spacing: -0.2px;

      color:$richDark;

      max-width: 280px;

      word-break: break-word;
    }
  }
  &-content{
    &__block{
      & + &{
        margin-top: 30px;
      }
    }
    &__text{
      font-size: 18px;
      line-height: 24px;

      color:$secondary;
      &-link{
        color:$accent;
        transition: .5s color;
        &:hover{
          color:$accentHover;
        }
      }
    }
    &__input{
      max-width: 470px;
      height: 60px;

      background: #FFFFFF;
      border: 1px solid $lightGrey;
      border-radius: 5px;
    }
  }
}


@media screen and (max-width: 600px) {
  .profile{
    &-content{
      &__text{
        &_center{
          &-mobile{
            text-align: center;
          }
        }
      }
    }
  }
}