.box-404{
  display: flex;
  align-items: center;
  justify-content: center;
  &__wrapper{
    max-width: 465px;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__title,&__desc{
    font-weight: 600;
    line-height: 1;
    color:$richDark;
    text-align: center;
  }
  &__title{
    font-size: 90px;
  }
  &__desc{
    font-size: 30px;
    margin-top: 30px;
  }
  &__btn{
    margin-top: 30px;

    height: 60px;
    max-width: 200px;
  }
}