body {
  font-family: 'Inter', sans-serif;

  font-size: 18px;
  line-height: 136%;

  color: #000;

  font-weight: 400;
}

.container {
  max-width: 1220px;
  padding: 0 25px;
  width: 100%;

  margin: 0 auto;
}

.page {
  &-wrapper {
    display: flex;
    flex-direction: column;

    min-height: 100vh;

    &__header, &__footer {
      flex: 0 0 auto;

      background-color: #fff;
    }

    &__content {
      flex: 1 0 auto;

      padding: 50px 0;

      background-color: #F9F9F9;

      &_big{
        padding: 80px 0;
      }
      &_banner{
        padding: 160px 0 115px 0;
      }
    }
  }

  &-content {
    &__header {
      position: relative;
      &-block {
        & + & {
          margin-top: 40px;
        }
      }

      &-back {
        position: absolute;
        left: 0;

        top: -40px;
      }

      &-back + &-block{
        padding-top: 40px;
      }

    }

    &__title {
      font-size: 30px;
      font-weight: 600;
      line-height: 36px;

      color: $monoBlack;
    }

    &__desc{
      margin-top: 40px;
    }

    &__header + &__wrapper{
      padding-top: 30px;
    }
  }

  &-tabs {
    &__nav {
      display: flex;
      align-items: center;

      &-item {
        width: 100%;
        max-width: 150px;
        height: 58px;

        color: $secondary;

        border-bottom: 2px solid transparent;

        transition: .5s border-bottom-color;

        &.active {
          color: $accent;

          border-bottom-color: $accent;

          &:hover {
            color: $accentHover;

            border-bottom-color: $accentHover;
          }
        }
      }

      &-link {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 100%;
        height: 100%;

        font-size: 17px;
        font-weight: 600;
        color: inherit;

        transition: .5s color;

        outline: none;
      }
    }

    &__content {
      padding-top: 40px;
    }
  }
}

.icon {
  width: 100%;
  height: 100%;
}

.input {
  &__item {
    -webkit-appearance: none;
    border: none;
    background: none;
    outline: none;

    width: 100%;
    height: 100%;

    padding: 0 16px;
    font-size: 18px;
    color: $monoBlack;
  }
}


.tabs {
  &-content {
    &__item {
      display: none;

      &.active {
        display: block;
      }
    }
  }
}

.btn {
  -webkit-appearance: none;

  border: none;
  outline: none;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  &-accent {
    background-color: $accent;

    font-size: 16px;
    font-weight: 500;
    color: #fff;

    border-radius: 4px;

    transition: .5s background-color;

    &:hover {
      background-color: $accentHover;
    }

    &-light {
      background-color: $accentSecondary;

      font-size: 17px;
      font-weight: 500;
      color: $accent;

      border-radius: 4px;

      transition: .5s background-color;
    }
  }
}

.back-link {
  width: 100px;
  height: 40px;
}


.link {
  color: $accent;

  transition: .5s color;

  &:hover {
    color: $accentHover;
  }
}

.delete-list{
  li + li{
    margin-top: 15px;
  }
  li a{
    text-decoration: underline;
  }
}

.progress{
  &-bar{
    width: 100%;
    height: 4px;

    background: $lightGrey;

    border-radius: 2px;

    position: relative;
    &-current{
      position: absolute;
      left:0;
      bottom: 0;
      width: 0;

      height: 100%;

      background: $accent;

      border-radius: 2px;
    }
  }
}

.trigger{
  &-link{
    &:hover{
      cursor: pointer;
    }
  }
  &-block{
    display: none;
    &.active{
      display: block;
      animation: showHide 0.5s forwards;
    }
  }
}

.popup{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  &.active{
    z-index: 9;
    display: block;
  }
  &__row{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    &-block{
      max-width: 200px;
    }
  }
  &__wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    background: rgba(0, 0, 0, 0.2);

    max-height: 100vh;
    overflow-y: auto;
    overflow-x: hidden;

    padding: 60px 25px;
  }
  &__content{
    max-width: 460px;
    width: 100%;

    padding: 60px 40px;

    background: #FFFFFF;
    border-radius: 20px;

    position: relative;
  }
  &__close{
    position: absolute;
    top: 40px;
    right: 40px;

    width: 20px;
    height: 20px;
    &-img{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
  &__btn{
    height: 60px;
  }
  &__title{
    font-weight: 600;
    font-size: 30px;
    line-height: 36px;

    color: #020203;
  }
  &__text{
    font-size: 18px;
    line-height: 24px;

    color:$monoBlack;
    &-bold{
      font-weight: 600;
    }
    &-accent{
      color:$accent;
    }
    &-grey{
      color: #95929C;
    }

  }
  &__block{
    & + &{
      margin-top: 24px;
    }
  }
  &__block + &__footer{
    margin-top: 60px;
  }
  &__input{
    border-color: #D2D2D2;

    color:#020203;

    position: relative;
    &.error{
      border-color: #FF7C7C;

      color:#FF7C7C;
    }
    &.succes{
      border-color: $monoBlack;

      color:$monoBlack;
    }
    &-item{
      -webkit-appearance: none;
      width: 100%;
      height: 60px;

      background: #FFFFFF;
      border: 1px solid;
      border-radius: 5px;
      padding: 15px;

      border-color: inherit;

      font-size: 18px;
      color:#020203;
      outline: none;

      transition: .5s all;
      &::placeholder{
        color: #95929C;
      }
      &:focus{
        border-color: #95929C;
      }
    }
    &-text{
      font-size: 18px;
      line-height: 24px;

      color: inherit;

      position: absolute;

      bottom: -55px;
      left: 0;
      max-width: 240px;
      height: 50px;
    }
  }
}

@media screen and (max-width: 1220px) and (min-width: 769px)  {
  .container{
    padding: 0 95px;
  }
}

@media screen and (max-width: 500px) {
  .page {
    &-tabs {
      &__nav {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
      }
    }
  }
}


@keyframes showHide {
  0%{
    display: none;
    opacity: 0;
  }
  50%{
    display: block;
  }
  100%{
    opacity: 1;
  }
}


@media screen and (max-width: 1200px) {
  .page{
    &-wrapper{
      &__content{
      }
    }
    &-content{
      &__header{
        &-back{
          position: relative;
          top: unset;
          left: unset;
        }
        &-back + &-block{
          margin-top: 30px;
        }
      }
    }
  }
}


@media screen and (max-width: 400px) {
  .popup{
    &__wrapper{
      align-items: flex-start;

      padding-top: 120px;
    }
    &__content{
      padding: 60px 20px;
    }
    &__title{
      text-align: center;
    }
    &__btn{
      text-align: left;
    }
  }
}