*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Inter';
  src: url("../fonts/Inter/Inter-Regular/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter/Inter-Regular/Inter-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: url("../fonts/Inter/Inter-Medium/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter/Inter-Medium/Inter-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: url("../fonts/Inter/Inter-SemiBold/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter/Inter-SemiBold/Inter-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: 'Inter', sans-serif;
  font-size: 18px;
  line-height: 136%;
  color: #000;
  font-weight: 400;
}

.container {
  max-width: 1220px;
  padding: 0 25px;
  width: 100%;
  margin: 0 auto;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.page-wrapper__header, .page-wrapper__footer {
  flex: 0 0 auto;
  background-color: #fff;
}

.page-wrapper__content {
  flex: 1 0 auto;
  padding: 50px 0;
  background-color: #F9F9F9;
}

.page-wrapper__content_big {
  padding: 80px 0;
}

.page-wrapper__content_banner {
  padding: 160px 0 115px 0;
}

.page-content__header {
  position: relative;
}

.page-content__header-block + .page-content__header-block {
  margin-top: 40px;
}

.page-content__header-back {
  position: absolute;
  left: 0;
  top: -40px;
}

.page-content__header-back + .page-content__header-block {
  padding-top: 40px;
}

.page-content__title {
  font-size: 30px;
  font-weight: 600;
  line-height: 36px;
  color: #020203;
}

.page-content__desc {
  margin-top: 40px;
}

.page-content__header + .page-content__wrapper {
  padding-top: 30px;
}

.page-tabs__nav {
  display: flex;
  align-items: center;
}

.page-tabs__nav-item {
  width: 100%;
  max-width: 150px;
  height: 58px;
  color: #95929C;
  border-bottom: 2px solid transparent;
  transition: .5s border-bottom-color;
}

.page-tabs__nav-item.active {
  color: #FF7940;
  border-bottom-color: #FF7940;
}

.page-tabs__nav-item.active:hover {
  color: #FF8E5F;
  border-bottom-color: #FF8E5F;
}

.page-tabs__nav-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 17px;
  font-weight: 600;
  color: inherit;
  transition: .5s color;
  outline: none;
}

.page-tabs__content {
  padding-top: 40px;
}

.icon {
  width: 100%;
  height: 100%;
}

.input__item {
  -webkit-appearance: none;
  border: none;
  background: none;
  outline: none;
  width: 100%;
  height: 100%;
  padding: 0 16px;
  font-size: 18px;
  color: #020203;
}

.tabs-content__item {
  display: none;
}

.tabs-content__item.active {
  display: block;
}

.btn {
  -webkit-appearance: none;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.btn-accent {
  background-color: #FF7940;
  font-size: 16px;
  font-weight: 500;
  color: #fff;
  border-radius: 4px;
  transition: .5s background-color;
}

.btn-accent:hover {
  background-color: #FF8E5F;
}

.btn-accent-light {
  background-color: #FFEEE6;
  font-size: 17px;
  font-weight: 500;
  color: #FF7940;
  border-radius: 4px;
  transition: .5s background-color;
}

.back-link {
  width: 100px;
  height: 40px;
}

.link {
  color: #FF7940;
  transition: .5s color;
}

.link:hover {
  color: #FF8E5F;
}

.delete-list li + li {
  margin-top: 15px;
}

.delete-list li a {
  text-decoration: underline;
}

.progress-bar {
  width: 100%;
  height: 4px;
  background: #D2D2D2;
  border-radius: 2px;
  position: relative;
}

.progress-bar-current {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 100%;
  background: #FF7940;
  border-radius: 2px;
}

.trigger-link:hover {
  cursor: pointer;
}

.trigger-block {
  display: none;
}

.trigger-block.active {
  display: block;
  animation: showHide 0.5s forwards;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.popup.active {
  z-index: 9;
  display: block;
}

.popup__row {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.popup__row-block {
  max-width: 200px;
}

.popup__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  max-height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 60px 25px;
}

.popup__content {
  max-width: 460px;
  width: 100%;
  padding: 60px 40px;
  background: #FFFFFF;
  border-radius: 20px;
  position: relative;
}

.popup__close {
  position: absolute;
  top: 40px;
  right: 40px;
  width: 20px;
  height: 20px;
}

.popup__close-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.popup__btn {
  height: 60px;
}

.popup__title {
  font-weight: 600;
  font-size: 30px;
  line-height: 36px;
  color: #020203;
}

.popup__text {
  font-size: 18px;
  line-height: 24px;
  color: #020203;
}

.popup__text-bold {
  font-weight: 600;
}

.popup__text-accent {
  color: #FF7940;
}

.popup__text-grey {
  color: #95929C;
}

.popup__block + .popup__block {
  margin-top: 24px;
}

.popup__block + .popup__footer {
  margin-top: 60px;
}

.popup__input {
  border-color: #D2D2D2;
  color: #020203;
  position: relative;
}

.popup__input.error {
  border-color: #FF7C7C;
  color: #FF7C7C;
}

.popup__input.succes {
  border-color: #020203;
  color: #020203;
}

.popup__input-item {
  -webkit-appearance: none;
  width: 100%;
  height: 60px;
  background: #FFFFFF;
  border: 1px solid;
  border-radius: 5px;
  padding: 15px;
  border-color: inherit;
  font-size: 18px;
  color: #020203;
  outline: none;
  transition: .5s all;
}

.popup__input-item::placeholder {
  color: #95929C;
}

.popup__input-item:focus {
  border-color: #95929C;
}

.popup__input-text {
  font-size: 18px;
  line-height: 24px;
  color: inherit;
  position: absolute;
  bottom: -55px;
  left: 0;
  max-width: 240px;
  height: 50px;
}

@media screen and (max-width: 1220px) and (min-width: 769px) {
  .container {
    padding: 0 95px;
  }
}

@media screen and (max-width: 500px) {
  .page-tabs__nav {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
  }
}

@keyframes showHide {
  0% {
    display: none;
    opacity: 0;
  }
  50% {
    display: block;
  }
  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 1200px) {
  .page-content__header-back {
    position: relative;
    top: unset;
    left: unset;
  }
  .page-content__header-back + .page-content__header-block {
    margin-top: 30px;
  }
}

@media screen and (max-width: 400px) {
  .popup__wrapper {
    align-items: flex-start;
    padding-top: 120px;
  }
  .popup__content {
    padding: 60px 20px;
  }
  .popup__title {
    text-align: center;
  }
  .popup__btn {
    text-align: left;
  }
}

