.filter{
  margin-bottom: 30px;
  &-wrapper{
    display: flex;
    align-items: center;
    &__search{
      width: 100%;
      max-width: 370px;
      height: 55px;
      &-input{
        -webkit-appearance: none;

        width: 100%;
        height: 100%;

        background: #EBEBEB;
        outline: none;
        border: none;
        border-radius: 10px;

        padding: 0 25px;

        color:$monoBlack;
        font-size: 18px;
      }
    }
    &__category{
      margin-left: 30px;
    }
  }
  &-category{
    &__list{
      display: flex;
      align-items: center;
      &-item{
        display: flex;
        align-items: center;

        color: $monoBlack;
        &.selected{
          color:$accent;
        }
        &.not-active{
          color:$secondary;

          &::after{
            background: url("../img/arrow-grey.svg") center center/contain no-repeat;
          }
        }
        &.active{
          color:$accent;
          &::after{
            background: url("../img/arrow-accent.svg") center center/contain no-repeat;
          }
        }
        &_mobile.active{
          display: none !important;
        }
        &.filter-block{
          &::after{
            display: none;
          }
        }
        &::after{
          content: '';
          width: 12px;
          height: 8px;
          display: block;

          background: url("../img/arrow-black.svg") center center/contain no-repeat;
          margin-left: 10px;
        }
        & + &{
          margin-left: 30px;
        }
      }
      &-link{
        font-size: 17px;
        font-weight: 600;
        color:inherit;
      }
    }
  }
  &-choise{
    margin-top: 30px;
  }
  &-choises{
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    margin: -10px;
    &__block{
      margin:5px;
    }
    &__btn{
      display: flex;
      align-items: center;
      &-icon{
        display: block;
        width: 24px;
        height: 24px;

        margin-right: 10px;
      }
      &-text{
        font-weight: 600;
        font-size: 17px;
        color:$accent;

        transition: .3s color;
        &:hover{
          color:$accentHover;
        }
      }
    }
  }
  &-checkbox{
    &__input{
      display: none;
      &:checked + .filter-checkbox__checkmark{
        background-color: $accent;
        color:#fff;
      }
    }
    &__checkmark{
      display: block;
      padding: 5px 10px;

      background: rgba(#EBEBEB,0.5);
      border-radius: 24px;

      color:rgba($monoBlack,.5);

      transition: 0.3s color, .3s background-color;
      &:hover{
        cursor: pointer;
      }
    }
  }

  &-block{
    display: none;
    &.active{
      display: block;
    }
  }
}

.catalog{
  &-preload{
    display: flex;
    align-items: center;
    justify-content: center;

    margin-top: 30px;
  }
}

@media screen and (max-width: 1110px) {
  .filter{
    &-wrapper{
      flex-direction: column;
      align-items: flex-start;
      &__category{
        margin: 30px 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 650px) {
  .filter{
    &-choise{
      &_desktop{
        display: none;
      }
    }
    &-category{
      &__list{
        &-item{
          &_mobile{
            &.active{
              display: block!important;
            }
          }
        }
      }
    }
    &-category{
      &__list{
        flex-direction: column;
        align-items: flex-start;
        &-item{
          & + &{
            margin: 30px 0 0 0;
          }
        }
      }
    }
  }
}