.header{
  z-index: 8;
  position: relative;

  background: #fff;
  &__wrapper{
    height: 110px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    background-color: #fff;
  }
  &__block{
    max-width: 228px;
    width: 100%;
    &-btn{
      display: flex;
      justify-content: flex-end;
    }
  }
  &__logo{
    height: 60px;
    &-img{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
  &__profile{
    display: flex;
    align-items: center;
    justify-content: flex-end;

    &-block + &-block{
      margin-left: 50px;
    }
  }
  &__notification{
    position: relative;
  }
  &__btn{
    max-width: 150px;
    height: 40px;
  }
  &-profile{
    position: relative;
    &__link{
      display: flex;
      align-items: center;

      &:hover{
        cursor: pointer;
      }
      &.active{
        .header-profile__link-icon{
          transform: rotate(180deg);
        }
      }
      &-icon{
        display: block;

        margin-left: 17px;

        transition: .5s all;
        svg{
          width: 14px;
          height: 8px;
        }
      }
    }
    &__img{
      width: 48px;
      height: 48px;

      border-radius: 50%;
      overflow: hidden;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
    &__list{
      position: absolute;
      right: -15px;
      top: 70px;
      width: 270px;

      padding: 10px;

      background: #FFFFFF;
      box-shadow: 0px 4px 45px rgba(0, 0, 0, 0.1);
      border-radius: 20px;

      display: none;
      &.active{
        display: block;
        z-index: 99;
        animation: showHide 0.5s forwards;
      }
    }
  }
  &-nav{
    &__list{
      display: flex;
      align-items: center;
    }
  }
  &-list{
    &__item + &__item{
      margin-left: 30px;
    }
    &__link{
      font-size: 17px;
      font-weight: 600;

      color: $monoBlack;
      transition: .3s color;
      &:hover{
        color:$accent;
      }
    }
  }
}


.notification{
  &-link{
    width: 30px;
    height: 35px;

    position: relative;
    &__count{
      position: absolute;
      right: -4px;
      top: 2px;

      width: 17px;
      height: 17px;

      display:flex;
      align-items: center;
      justify-content: center;

      border-radius: 50%;

      background-color: $accent;

      font-size: 10px;
      color:#fff;
      letter-spacing: -0.306546px;

      transition: .5s background-color;
      &:hover{
        background-color: $accentHover;
      }
    }
  }
  &-block{
    position: absolute;
    right: -15px;
    top: 70px;
    width: 390px;
    padding: 25px;

    background: #FFFFFF;
    box-shadow: 0px 4px 45px rgba(0, 0, 0, 0.1);
    border-radius: 20px;

    display: none;
    &.active{
      display: block;
      z-index: 99;
      animation: showHide 0.5s forwards;
    }
    &__title{
      font-weight: 600;
      font-size: 25px;
      line-height: 30px;
      letter-spacing: -0.2px;
      color: #232B3F;
    }
    &__content{
      margin-top: 30px;

      max-height: 300px;
      overflow: hidden auto;
    }
    &__footer{
      margin-top: 30px;
    }
    &__btn{
      height: 60px;
    }
  }
  &-item{
    & + &{
      margin-top: 30px;
    }
    &_read{
      .notification-item__wrapper{
        &::before{
          opacity: 0;
        }
      }
    }
    &__wrapper{
      display: flex;
      align-items: flex-start;

      &::before{
        content: '';
        width: 7px;
        height: 7px;
        display: block;

        border-radius: 50%;

        background-color: $accent;
        margin-right: 10px;

        transform: translateY(7px);
      }
    }
    &__time{
      color: rgba(0, 0, 0, 0.5);
      font-size: 18px;
    }
    &__content{
      margin-left: 10px;
    }
    &__text{
      color: #020203;
      font-size: 18px;
      line-height: 24px;
    }
  }
}

.page{
  &-nav{
    width: 100%;
    &__item{
      display: none;
      &_desktop{
        display: block;
      }
      &-link{
        display: flex;
        align-items: center;

        width: 100%;
        height: 55px;
        border-radius: 10px;

        padding: 0 18px;

        transition: .5s background-color;
        &:hover{
          background-color: rgba(#EBEBEB80,.5);
        }
        &_not-read{
          position: relative;

          &::after{
            content: '';
            width: 7px;
            height: 7px;
            display: block;
            border-radius: 50%;
            margin-left: auto;
            background-color: $accent;
          }
        }
      }
      &-icon{
        height: 19px;
        width: 19px;
        flex-shrink: 0;
        svg{
          width: 100%;
          height: 100%;
        }
      }
      &-text{
        margin-left: 15px;

        font-weight: 600;
        font-size: 17px;
        color: #232B3F;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .header{
    &__nav{
      display: none;
    }
  }
  .page{
    &-nav{
      &__item{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 600px) {
  .header{
    &__notification{
      display: none;
    }
    &__btn{
      max-width: 100px;
    }
  }
  .notification{
    &-block{
      width: 100vw;
      right: 0;

      top: 150px;
    }
  }
}