.footer{
  position: relative;
  z-index: 8;

  background: #fff;
  &__wrapper{
    background: #fff;

    padding: 40px 0 35px 0;
  }
  &__row{
    display: flex;
    justify-content: space-between;
    & + &{
      margin-top: 45px;
    }
  }
  &__logo{
    height: 60px;
    max-width: 228px;
    width: 100%;
    &-img{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
  &__copyright{
    color:$monoBlack;
    font-size: 18px;
  }
  &__link{
    font-weight: 600;
    font-size: 17px;

    color:$accent;

    text-align: right;

    transition: .5s color;
    &:hover{
      color:$accentHover;
    }
  }
  &-social{
    &__text{
      color:#000;
      font-size: 18px;
    }
    &__list{
      margin-top: 15px;

      display: flex;
      align-items: center;
      justify-content: flex-end;
      &-item + &-item{
        margin-left: 12px;
      }
      &-link{
        display: flex;
        align-items: center;
        justify-content: center;

        width: 32px;
        height: 32px;
        border-radius: 50%;
        background: #BABABA;

        transition: .5s background-color;
        &:hover{
          background-color: #CBCBCB;
        }
      }
      &-icon{
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}


@media screen and (max-width: 767px) {
  .footer{
    &__row{
      align-items: center;
    }
    &-social{
      &__list{
        margin-top: 0;
      }
      &__text{
        display: none;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .footer{
    &__logo{
      height: 40px;
    }
    &__wrapper{
      padding: 25px 0 35px 0;
    }
    &__row{
      flex-direction: column;
      & + &{
        margin-top: 20px;
      }
    }
    &__block{
      & + &{
        margin-top: 20px;
      }
    }
  }
}