.certificate{
  &-card{
    &__wrapper{
      padding: 30px;

      max-width: 670px;
      width: 100%;


      background: #FFFFFF;
      box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.08);
      border-radius: 20px;
    }
    &__text{
      font-size: 18px;
      line-height: 24px;

      color:$secondary;
    }
    &__name{
      margin-left: 15px;

      font-size: 21px;
      line-height: 24px;
      letter-spacing: -0.2px;
      font-weight: 600;

      color:$monoBlack;
    }
    &__content{
      margin-top: 15px;

      display: flex;
      align-items: center;
    }
    &__footer{
      margin-top: 25px;
    }
    &__btn{
      max-width: 200px;
      height: 40px;
    }
  }
  &-info{
    &__title{
      color:$accent;

      text-align: center;
    }
    &__date{
      margin-top: 15px;

    }
    &__text{
      font-size: 18px;
      line-height: 24px;

      color:$secondary;

      text-align: center;

    }
    &__download{
      margin-top: 40px;

      height: 60px;
    }
    &__share{
      margin-top: 20px;
      &-list{
        margin-top: 20px;

        display: flex;
        align-items: center;
        justify-content: center;
      }
      &-item{
        & + &{
          margin-left: 15px;
        }
      }
      &-icon{
        width: 36px;
        height: 36px;

        display: block;
        svg{
          width: 100%;
          height: 100%;
        }
      }
    }
    &__btn{
      margin-top: 40px;

      height: 60px;
    }
  }
  &-single{
    &__wrapper{
      height: 1100px;

      padding: 60px 75px 55px 75px;

      background: url("../img/certificate-bg.svg") center center/cover no-repeat, #fff;

      box-shadow: 0px 4px 45px rgba(0, 0, 0, 0.1);
      border-radius: 15px;

      display: flex;
      flex-direction: column;
    }
    &__logo{
      width: 250px;
      height: 70px;
      &-img{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
    &__title{
      font-weight: 600;
      font-size: 70px;
      line-height: 1;

      color:$monoBlack;
      text-transform: uppercase;
    }
    &__text{
      margin-top: 35px;

      font-size: 25px;
      line-height: 30px;

      color:$monoBlack;
      letter-spacing: -0.2px;
    }
    &__name{
      margin-top: 35px;

      &-text{
        color:$accent;

        font-size: 40px;
        line-height: 48px;
        font-weight: 600;
      }
    }
    &__course{
      &-text{
        font-size: 24px;
        line-height: 33px;
        color:$monoBlack;
        &-title{
          color:$accent;
          font-weight: 700;
        }
      }
    }
    &__date{
      &-text{
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;

        color:$monoBlack;
      }
      &-item{
        font-weight: 400;
        font-size: 24px;
        line-height: 29px;

        color: $monoBlack;
      }
    }
    &__footer,&__content,&__course{
      margin-top: auto;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: 275px auto;
    gap: 125px;
  }
}

@media screen and (max-width: 1100px) {
  .certificate{
    &-single{
      &__wrapper{
        height: 520px;
        padding: 35px 30px;
      }
      &__logo{
        width: 120px;
        height: 32px;
      }
      &__title{
        font-size: 33px;
        line-height: 41px;
      }
      &__text{
        margin-top: 15px;
        font-size: 12px;
        line-height: 15px;
        letter-spacing: -0.01px;
      }
      &__name{
        margin-top: 15px;
        &-text{
          font-size: 19px;
          line-height: 23px;
        }
      }
      &__course{
        &-text{
          font-size: 12px;
          line-height: 16px;
        }
      }
      &__date{
        &-text,&-item{
          font-size: 12px;
          line-height: 14px;
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .certificate{
    &__grid{
      grid-template-columns: minmax(0,1fr);
      gap: 40px;
      &-item{
        &:nth-child(2){
          order: 1;
        }
        &:nth-child(1){
          order: 2;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .certificate{
    &-card{
      &__content{
        align-items: flex-start;
        flex-direction: column;
      }
      &__name{
        margin: 15px 0 0 0;
      }
    }
  }
}