.courses{
  &-empty{
    display: flex;
    align-items: center;
    justify-content: center;

    &__wrapper{
      max-width: 370px;
      width: 100%;
    }
    &__img{
      width: 100px;
      height: 80px;

      margin: 0 auto;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
    &__content{
      margin-top: 60px;
    }
    &__text{
      font-size: 18px;
      line-height: 24px;

      color:$monoBlack;

      text-align: center;
    }
    &__footer{
      margin-top: 35px;
    }
    &__btn{
      height: 60px;
    }
  }
  &-grid{
    display: grid;
    grid-template-columns: repeat(3,minmax(0,1fr));
    grid-auto-rows: 580px;
    gap: 45px 30px;
  }
  &-card{
    height: 100%;
    &__item{
      border-radius: 15px;

      overflow: hidden;

      background-color: #fff;
      box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.08);

      display: flex;
      flex-direction: column;
      height: 100%;

      transition: 0.5s all;
      &:hover{
        box-shadow: 0px 4px 45px rgba(0, 0, 0, 0.1);
      }
    }
    &__header{
      height: 250px;

      position: relative;
    }
    &__start{
      position: absolute;
      top: 20px;
      right: 20px;

      display: block;
      padding: 6px 9px;

      background-color: $accent;
      border-radius: 4px;


      font-weight: 600;
      font-size: 17px;
      color: #FFFFFF;
    }
    &__img{
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
    &__wrapper{
      padding: 30px 20px 20px 20px;

      height: 100%;
      display: flex;
      flex-direction: column;
    }
    &__title{
      font-weight: 600;
      font-size: 25px;
      line-height: 30px;
      letter-spacing: -0.2px;

      color: #000000;
    }
    &__text{
      font-size: 18px;
      line-height: 24px;
      color: $monoBlack;

      margin-top: 10px;
    }
    &__footer{
      padding-top: 15px;
      margin-top: auto;

      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__btn{
      height: 40px;
      max-width: 150px;
    }
    &__time{
      display: flex;
      align-items: center;
      justify-content: center;

      flex-wrap: wrap;

      font-weight: 600;
      font-size: 17px;
      color:$secondary;

      &::before{
        content: '';
        width: 24px;
        height: 24px;
        display: block;

        background: url("../img/clock.svg") center center/contain no-repeat;

        margin-right: 8px;
      }
    }
  }
  &-search{
    &-failed{
      display: flex;
      align-items: center;
      justify-content: center;

      padding-top: 60px;
      &__wrapper{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        max-width: 370px;
        width: 100%;
      }
      &__img{
        width: 100px;
        height: 100px;

        object-fit: contain;
      }
      &__text{
        margin-top: 50px;

        text-align: center;

        font-size: 18px;
        color: #020203;
      }
    }
  }
}


@media screen and (max-width: 1050px) {
  .courses{
    &-grid{
      grid-template-columns: repeat(2,minmax(0,1fr));
    }
  }
}

@media screen and (max-width: 767px) {
  .courses{
    &-grid {
      grid-template-columns: minmax(0, 1fr);
      grid-auto-rows: auto;
    }
    &-card{
      &__header{
        height: 160px;
      }
      &__footer{
        margin-top: 30px;
        padding: 0;
      }
    }
  }
}